/*
Sidebar css
*/
.sidebar {
  float: left;
  position: relative;
  width: 18%;
  height: 100vh;
  background: #2c2c35;
  color: #fff;
  border-right: 1.5px solid #393942;

  .header {
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1.5px solid #393942;
    .header-text {
      font-size: 1.6em;
      width: 90%;
      text-align: center;
      font-weight: lighter;
    }
    .header-add-user {
      cursor: pointer;
      margin-left: auto;
      margin-right: 10px;
      &:hover {
        color: #2793c9;
      }
    }
  }

  .sidebar-dynamic-content {
    height: calc(100vh - 8vw);
    overflow: auto;
    text-align:justify;
    .friend-list {
      .user {
        cursor: pointer;
        height: 50px;
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: 10px 0;
        &:hover {
          background-color: #20222b;
        }
        .profile-pic {
          width: 40px;
          height: 40px;
          border-radius: 50%;
          background: #2793c9;
          text-align: center;
          line-height: 40px;
          margin: 0 10px;
          font-size: 1.2em;
          img {
            width: 35px;
            height: 35px;
            border-radius: 50%;
            object-fit: cover;
          }
        }
        .user-detail {
          display: flex;
          flex-direction: column;
          font-size: .8em;
          .last-seen {
            font-size: .8em;
            color: #A2A3A5;
          }
        }
        .online-status {
          order: 2;
          margin-left: auto;
          line-height: 32px;
          padding: 10px;
          .online {
            width: 8px;
            height: 8px;
            border-radius: 50px;
            background-color: #31cd31;
          }
          .offline {
            width: 8px;
            height: 8px;
            border-radius: 50px;
            background-color: #83848F;
          }
        }
      }
    }

    .settings {
      margin-top: 20px;
      .header-text {
        font-size: 1.1em;
      }
      .settings-menu {
        margin-top: 10px;
        font-size: .9em;
        color: #A2A3A5;
        .menu-item {
          cursor: pointer;
          padding: 10px 0;
          display: flex;
          align-items: center;
          justify-content: space-between;
          i {
            margin-right: 10px;
          }
          &:not(:last-child)::after {
            margin: auto 20px;
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            content: "\f105";
          }
          &:hover {
            background-color: #20222b;
          }
        }
      }
    }
  }

  .footer {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    left: 0;
    bottom: 0;
    right: 0;
    height: 55px;
    border-top: 1.5px solid #393942;

    .footer-item {
      cursor: pointer;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      &:hover {
        color: #2793c9;
      }
    }
  }
}
